interface ObjectCardProps {
  searchStore: SearchStore;
  mapglStore: MapglStore;
  directionsStore: DirectionsStore;
  routTime?: string;
  setIsDirectionCard: (value: boolean) => void;
}

export const ObjectCard: FC<ObjectCardProps> = observer(
  ({ searchStore, mapglStore, directionsStore, routTime, setIsDirectionCard }) => {
    const [isOpen, setIsOpen] = useState(false);
    const [isCompaniesListOpen, setCompaniesListIsOpen] = useState(false);

    const setType = (type?: string) => {
      for (let item of Places) {
        if (type === item.type) {
          return item.title;
        }
      }
      return "";
    };

    return (
      <Swipe
        onSwipeUp={() => {
          setIsOpen(!!searchStore.currentObject?.links?.branches?.count);
        }}
        onSwipeDown={() => {
          setIsOpen(false);
        }}
      >
        <Wrapper
          style={{
            display: searchStore.currentObject ? "flex" : "none",
            height: isOpen ? "600px" : "230px",
          }}
        >
          <IconButtonContainer
            onClick={() => {
              setIsDirectionCard(true);
            }}
          >
            <IconButton
              style={{
                top: isOpen ? "500px" : "-35px",
                transition: "top 0.3s",
              }}
            />
          </IconButtonContainer>
          {!!searchStore.currentObject?.links?.branches?.count ? (
            <SwipeIndicator />
          ) : null}
          <UpperInfo
            style={{
              flexDirection: isOpen ? "row" : "column",
              justifyContent: isOpen ? "space-around" : "",
              alignItems: isOpen ? "center" : "",
            }}
          >
            {isOpen ? <ArrowDownIcon /> : null}
            <PlaceTitle>
              {searchStore.currentObject?.name
                ? searchStore.currentObject?.name
                : "Utility structure"}
            </PlaceTitle>
            {!isOpen ? (
              <PlaceInfoWrapper>
                <PlaceInfo>
                  {setType(searchStore.currentObject?.type)}
                </PlaceInfo>
                <PlaceInfo>{routTime}</PlaceInfo>
              </PlaceInfoWrapper>
            ) : (
              <ShareIcon style={{ marginLeft: "11px" }} />
            )}
          </UpperInfo>
          <Divider />
          {searchStore.currentObject?.address ? (
            <BottomInfo>
              <GreenMarkerIcon />
              <BottomText>
                {searchStore.currentObject?.address?.postcode &&
                  searchStore.currentObject.address?.postcode + ", "}
                {searchStore.currentObject?.address_name}
              </BottomText>
              {!isOpen ? <ShareIcon /> : null}
            </BottomInfo>
          ) : null}
          {!!searchStore.currentObject?.links?.branches?.count ? (
            <BottomInfo>
              <BuildingIcon />
              <BottomText>
                {searchStore.currentObject?.floors
                  ? `${searchStore.currentObject?.floors.ground_count} floor | ${searchStore.currentObject?.links?.branches?.count} companies`
                  : `${searchStore.currentObject?.links?.branches?.count} companies`}
              </BottomText>
            </BottomInfo>
          ) : null}

          <BottomInfo>
            <WorkTimeIcon />
            <BottomText>Daily 8 AM  10 PM Open</BottomText>
          </BottomInfo>
          <BottomInfo>
            <PhoneIcon />
            <BottomText>+357 22006200</BottomText>
          </BottomInfo>
          <BottomInfo>
            <InternetIcon />
            <BottomText>www.themallofengomi.com</BottomText>
          </BottomInfo>
          {isOpen ? (
            <div>
              <Divider
                style={{
                  height: "2px",
                }}
              />
              <Title>ompanies</Title>
              <BottomInfo
                onClick={() => setCompaniesListIsOpen(true)}
                style={{
                  height: "72px",
                  width: "100%",
                  borderBottom: "solid 1px rgba(33, 33, 33, 0.08)",
                  alignItems: "center",
                  margin: "18px 0",
                }}
              >
                <BuildingsIcon />
                <div
                  style={{
                    width: "360px",
                  }}
                >
                  <BottomText>All Companies</BottomText>
                  <AdditionalInfo>{`${searchStore.currentObject?.links?.branches?.count} companies`}</AdditionalInfo>
                </div>
                <ArrowRightIcon />
              </BottomInfo>
            </div>
          ) : null}
        </Wrapper>
        {isCompaniesListOpen && (
          <CompaniesList setIsOpen={setCompaniesListIsOpen} />
        )}
      </Swipe>
    );
  }
);
