import styled from "styled-components";
import Check from "assets/images/check.svg";
import { useEffect, useState } from "react";
import { DirectionsStore } from "stores/DirectionsStore";
import { SearchStore } from "stores/SearchStore";
import { observer } from "mobx-react";
import { MapglStore } from "stores/MapglStore";

const DirectionCardWrapper = styled.div`
  position: absolute;
  bottom: 0;
  background: #ffffff;
  z-index: 52;
  width: 100%;
  height: 220px;
  text-align: center;
`;

const DirectionButtonsWrapper = styled.div`
  width: 100%;
  height: 60px;
  display: grid;
  grid-template-columns: 50% 50%;
  padding: 10px 16px 0 16px;
  margin-bottom: 24px;
`;

const DirectionButton = styled.div`
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: 50% 50%;
  grid-template-rows: 50% 50;
  padding: 9px;
`;

const RouteTime = styled.div`
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.15px;
  color: rgba(0, 0, 0, 0.87);
`;

const Distance = styled.div`
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.25px;
  color: rgba(0, 0, 0, 0.6);
`;

const LetsGoButton = styled.button`
  width: 315px;
  height: 48px;
  background: #23b064;
  border-radius: 12px;
  margin-bottom: 10px;

  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 16px;
  text-align: center;
  color: #ffffff;
`;

const DirectionsLength = styled.div`
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 0.25px;
  color: rgba(0, 0, 0, 0.6);
`;

const CheckIcon = styled.div`
  background-image: url(${Check});
  background-repeat: no-repeat;
  background-position: center;
  width: 100%;
  height: 20px;
`;

enum DirectionType {
  faster = "jam",
  shorter = "shortest",
}

interface DirectionCardProps {
  directionsStore: DirectionsStore;
  mapglStore: MapglStore;
  searchStore: SearchStore;
}

export const DirectionCard = observer(
  ({ directionsStore, searchStore, mapglStore }: DirectionCardProps) => {
    const [selectedDirection, setSelectedDirection] = useState(
      DirectionType.faster
    );

    const [faster, setFaster] = useState<any>(null);
    const [shortest, setShortest] = useState<any>(null);

    const buildDirection = (endPoint: number[], type: string) => {
      directionsStore.buildDirection(endPoint, type);
    };

    useEffect(() => {
      searchStore.currentObject &&
        directionsStore
          .getDirection(
            [
              searchStore.currentObject.point.lon,
              searchStore.currentObject.point.lat,
            ],
            DirectionType.faster
          )
          .then((res) => {
            setFaster(res.data[0]);
          });
      searchStore.currentObject &&
        directionsStore
          .getDirection(
            [
              searchStore.currentObject.point.lon,
              searchStore.currentObject.point.lat,
            ],
            DirectionType.shorter
          )
          .then((res) => {
            setShortest(res.data[0]);
          });
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    useEffect(() => {
      searchStore.currentObject &&
        buildDirection(
          [
            searchStore.currentObject.point.lon,
            searchStore.currentObject.point.lat,
          ],
          selectedDirection
        );
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [selectedDirection, directionsStore, searchStore.currentObject]);

    return (
      <DirectionCardWrapper>
        <DirectionButtonsWrapper>
          <DirectionButton
            onClick={() => {

              mapglStore.destroyPolygon();
              mapglStore.destroyMarker();
              mapglStore.destroyDirection();
              setSelectedDirection(DirectionType.faster);
            }}
            style={
              selectedDirection === DirectionType.faster
                ? {
                    border: "1px solid  #dafae8",
                    background: "#92c9ab",
                    borderRadius: "3px",
                  }
                : {}
            }
          >
            <RouteTime>{(shortest?.duration / 60).toFixed(1)} min</RouteTime>
            <CheckIcon />
            <Distance>{(shortest?.distance / 1000).toFixed(1)} km</Distance>
            <div>shorter </div>
          </DirectionButton>
          <DirectionButton
            onClick={() => {
              mapglStore.destroyPolygon();
              mapglStore.destroyMarker();
              mapglStore.destroyDirection();
              setSelectedDirection(DirectionType.shorter);
            }}
            style={
              selectedDirection === DirectionType.shorter
                ? {
                    border: "1px solid  #dafae8",
                    background: "#92c9ab",
                    borderRadius: "3px",
                  }
                : {}
            }
          >
            <RouteTime>{(faster?.duration / 60).toFixed(1)} min</RouteTime>
            <div></div>
            <Distance>{(faster?.distance / 1000).toFixed(1)} km</Distance>
            <div>faster </div>
          </DirectionButton>
        </DirectionButtonsWrapper>
        <LetsGoButton>Let`s go</LetsGoButton>
        <DirectionsLength>You have 28 routes left</DirectionsLength>
      </DirectionCardWrapper>
    );
  }
);
