import Marker from "assets/images/marker.svg";
import Building from "assets/images/building.svg";
import Branch from "assets/images/branch.svg";
import GeoObject from "assets/images/geo-object.svg";
import { ResponseObjectTypes } from "enums/ResponseObjectTypes.enum";
import { observer } from "mobx-react";
import { SearchStore } from "stores/SearchStore";
import styled from "styled-components";
import { MapglStore } from "stores/MapglStore";
import { ResultItem } from "api/interfaces/search.interface";

const Wrapper = styled.div`
  flex-direction: column;
  background: #ffffff;
  position: absolute;
  top: 120px;
  z-index: 2;
  width: 100%;
  height: 700px;
  border-radius: 20px 20px 0px 0px;
  overflow: scroll;
`;

const ItemWrapper = styled.div`
  min-height: 72px;
  display: flex;
  align-items: center;
  border-top: 3px solid #fafafafa;
`;

const ResultIcon = styled.p`
  width: 14px;
  height: 20px;
  margin-left: 29px;
`;

const TextWrapper = styled.div`
  display: flex;
  flex-direction: column;
`;
const ItemName = styled.div`
  margin-left: 29px;
`;

const ItemDescription = styled.div`
  margin-left: 29px;
  color: rgba(0, 0, 0, 0.6);
  line-height: 20px;
  letter-spacing: 0.25px;
  font-size: 14px;
`;

const NotFoundWrapper = styled.div`
  margin-left: 16px;
  margin-top: 39px;
`;

const NotFoundTitle = styled.div`
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 0.15px;
  color: #000000;
`;

const NotFoundDescription = styled.div`
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: 0.5px;
  color: #000000;
`;

interface ResultsProps {
  searchStore: SearchStore;
  mapglStore: MapglStore;
  setOpenObjectCard: (value: boolean) => void;
  searchInputRef: React.RefObject<HTMLInputElement>;
  isEnterPressed: boolean;
}

export const Results = observer(
  ({
    searchStore,
    setOpenObjectCard,
    mapglStore,
    searchInputRef,
    isEnterPressed
  }: ResultsProps) => {
    const getIcon = (type: ResponseObjectTypes) => {
      if (type === ResponseObjectTypes.branch) {
        return Branch;
      } else if (
        type === ResponseObjectTypes.admDiv ||
        type === ResponseObjectTypes.admDivCity ||
        type === ResponseObjectTypes.admDivCountry ||
        type === ResponseObjectTypes.admDivDistrict ||
        type === ResponseObjectTypes.admDivDistrictArea ||
        type === ResponseObjectTypes.admDivPlace ||
        type === ResponseObjectTypes.admDivRegion ||
        type === ResponseObjectTypes.admDivSettlement
      ) {
        return Marker;
      } else if (type === ResponseObjectTypes.building) {
        return Building;
      } else {
        return GeoObject;
      }
    };

    const handleClick = (item: any) => {
      searchStore.setCurrentObject(item);
      mapglStore.loadMapglMarker({
        coordinates: [item.point.lon, item.point.lat],
      });
      mapglStore.setCenter([item.point.lon, item.point.lat]);
      setOpenObjectCard(true);
    };
    return (
      <Wrapper
        style={{
          display: searchInputRef?.current?.value ? "flex" : "none",
        }}
      >
        {searchStore.searchResults?.result?.items.map((item: ResultItem) => {
          return (
            <ItemWrapper key={item.id} onClick={() => handleClick(item)}>
              <ResultIcon
                style={{
                  backgroundImage: `url(${getIcon(item.type)})`,
                }}
              ></ResultIcon>
              <TextWrapper>
                <ItemName>{item.name}</ItemName>
                <ItemDescription>
                  {(item.address_name &&
                    item.address?.postcode &&
                    item.address_name + ", " + item.address?.postcode) ||
                    item.name || item.full_name}
                </ItemDescription>
              </TextWrapper>
            </ItemWrapper>
          );
        })}
        {isEnterPressed && searchStore.searchResults?.meta.code === 404 && (
          <NotFoundWrapper>
            <NotFoundTitle>No Results Found</NotFoundTitle>
            <NotFoundDescription>
              Try searching for something else, check for typos or rephrase it
            </NotFoundDescription>
          </NotFoundWrapper>
        )}
      </Wrapper>
    );
  }
);
